.footer {
  display: grid;
  grid-template-columns: 2fr 1fr;
  -ms-flex-align: center;
      align-items: center;
  width: 88%;
  margin: auto;
  bottom: 0;
  height: auto;
}
.social {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: distribute;
      justify-content: space-around;
}
.by {
  font-size: 18px;
}
.social-media {
  font-size: 1.8em;
  color: #000;
  text-decoration: none;
}
@media(max-width: 769px) {
  .footer { width: 100%; grid-template-columns: 1fr; justify-items: center; }
  .social { -ms-flex-direction: column; flex-direction: column; }
}


